







































































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";

@Component({
  components: {
    Swipe
  },
  mixins: [mixin]
})
export default class User extends Vue {
  couponId = ''
  status = 'UNUSED'
  page = 0
  pageSize =  10
  discList = [];
  coupTime = "";
  //领券中心
  skip(){
    this.$router.push({
      path: "/discount",
    });
  }
  discountList(status){
    if(this.status == status){
       return;
    }
    if(!status){
        status = this.status 
    }
     this.status = status
     Vue.prototype.$reqFormPost1(
        "/coupon/user/linklist",
        {
          token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].token,
          userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].userId,
          status: this.status,
          page: this.page,
          pageSize: this.pageSize,
        },
        res => {
          if (res.returnCode != 200) {
            this["$Message"].warning(res.message);
            return;
          }
          this.discList = res.data.couponList;
        }
      );
  }
  goshop(){
    this.$router.push({
      path:"/"
    })
  }
  mounted() {
    this.discountList(false);
    //定位当前页面左侧样式
    this.$emit('selectMenu',{
      name: '优惠券',
      url:'/discountLobby',
    })
  }
}



